.header {
    position: sticky;
    top: 0;
    height: 11.4rem;

    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 3;
}

/*title styling*/
.header .title {
    position: sticky;
    top: 0;
    font-size: 1.2rem;
    box-shadow: 0 0 0.8rem rgba(127, 127, 127, 0.15);
    display: flex;
    justify-content: space-between;

    width: 100%;
    transition: height .2s ease-out, background-color .2s ease-out;
}
.header .title button {border-radius: 0}
.header.big .title {
    height: 5.8rem;
    background-color: #FFFFFF;
}
.header.small .title {
    height: 4.6rem;
    background-color: #F3F4F7;
}

.header .title>div {
    flex-grow: 1;
}
.logo-container {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: visible;
    min-width: 16rem;

    transition: width .17s ease-out;
}
.big .logo-container {
    width: 100%;
}
.small .logo-container {
    width: 0;
}

.side-btn {
    height: 100%;
    border: 0;
    padding: 0;
    font: inherit;
    display: flex;
    justify-content: center;
    align-items: center;

    transition: width .2s ease-out;
}
.big .side-btn {
    width: 13.6rem
}
.small .side-btn {
    width: 4.6rem;
}

text {
    overflow: hidden;
    white-space: nowrap;
    transition: width .2s ease-out, opacity .15s ease-out;
}
.big text {
    width: 5rem;
    opacity: 1;
}
.small text {
    width: 0;
    opacity: 0;
}

.header .menu {background: #dbdce2;}
.header .menu:hover {background: #d0d1d9;}

.header .settings {
    color: #FFFFFF;
    background: #49C5B6;
}
.header .settings:hover {
    background: #46bdaf;
}

/*tools styling*/
.toolbar {
    position: absolute;
    width: auto;

    margin: auto;
    box-sizing: border-box;

    padding-block: 1rem;
    display: flex;

    gap: 1rem;
    justify-content: center;
    transition: left .2s ease-out, right .2s ease-out, top .2s ease-out;
}
.big .toolbar {
    top: 6.3rem;
    left: 2rem;
    right: 2rem;
}
.small .toolbar {
    top: 0;
    left: max(20.6rem, calc(70% - 30rem));
    right: 6.6rem;
}

.toolbar button.refresh {
    height: 2.6rem;
    width: 2.6rem;
    flex-shrink: 0;
    border-radius: 1.3rem;
    background: #FFFFFF;
    box-shadow: 0 0 0.8rem rgba(127, 127, 127, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;
}

.rotate {animation: rotate 0.5s ease-in-out infinite;}
@keyframes rotate { 100% {transform: rotate(360deg);} }
